import { NavParams, NavController } from '../../../node_modules/ionic-angular/umd';

/**
 * Generated class for the SenatorInfoComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'senator-info',
  templateUrl: 'senator-info.html'
})
export class SenatorInfoComponent {

  text: string;

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    this.text = 'Informações sobre o papel do parlamentar na política Brasileira';
  }

}



